const expression = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/;


// funzione per post su Rebrandly
const headers = {
  'Content-Type': "application/json",
  apikey: "228dc84b36504cd2b6f86693f861bb46"
}

export const postUrltoRebrandly = (requestedURL, cb) => {
  var payload = {
    destination: requestedURL,
    domain: { fullName: "rebrand.ly" }
    //, slashtag: "A_NEW_SLASHTAG"
    //, title: "Rebrandly YouTube channel"
  } 
  var params = {
    headers: headers,
    contentType: 'application/json',
    method: 'POST',
    body: JSON.stringify(payload),
    muteHttpExceptions: true
  }
  fetch("https://api.rebrandly.com/v1/links", params)
    .then((response) => {
      return response.json();
    })
    .then((json) => {
      // Qui ho la risposta del server
      console.log(json);
      cb(json);
    })
    .catch((error) => {
      console.error("ERRORE NEL FETCH!");
      console.error(error);
    });
};

