export const deleteDataFromRebrandly = (id ,cb) => {
 
  var params = {
    headers: headers,
    contentType: 'application/json',
    method: 'DELETE',
   // body: JSON.stringify(payload),
    muteHttpExceptions: true
  }
  fetch("https://api.rebrandly.com/v1/links/" +id , params)
    .then((response) => {
      return response.json();
    })
    .then((json) => {
      // Qui ho la risposta del server
      console.log(json);
      cb(json);
    })
    .catch((error) => {
      console.error("ERRORE NEL FETCH!");
      console.error(error);
    });
};


export const validateUrl = (input) => {
  if (expression.test(input)) {
    return true;
  } else {
    return false;
  }
};

export const handleSaveToPC = (jsonData) => {
  const fileData = JSON.stringify(jsonData);
  const blob = new Blob([fileData], { type: "text/plain" });
  const url = URL.createObjectURL(blob);
  console.log("ecco l url:" + url);
  const link = document.createElement("a");
  link.download = "filename.json";
  link.href = url;
  link.click();
  URL.revokeObjectURL(blob);
};



