import React from "react";
import Head from "./Head.js";
import Input from "./Input.js";
import Footer from "./Footer.js";
import UrlList from "./UrlList.js";
import classNames from "classnames";
import { postUrltoRebrandly, getDataFromRebrandly, deleteDataFromRebrandly, validateUrl, handleSaveToPC } from "./utils.js";
import "./main.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input: "",
      getApi: null,
	  validUrl: true,
	  lastDelete:null,
      choosenBtn: null,
    };
  }

  setCopyBtn(choosen) {
    this.setState({ choosenBtn: choosen });
    //handleSaveToPC(this.state.data);
  }

  // function for:
  // 1) check for the url to be valid
  // 2) fetch  the URL from the API (external function from utils.js)
  // 3) update the state with the new "data"

  postUrl() {
	//let pushArray = this.state.getApi.slice();
	let newArray= this.state.getApi.slice();
    let url = this.state.input;
    if (!validateUrl(url)) {
      return null;
	}
	// Chiamata con POST a ReBrandly per caricare un nuovo URL
    postUrltoRebrandly(url, (json) => {
		newArray.unshift(json);
		this.setState({
			getApi: newArray
		})   
	});	
	getDataFromRebrandly( (json)=>{
		this.setState({
			getApi: json
		})
	});
    this.resetInput();
  }

  deleteApiData(data){
	  const newArray= this.state.getApi.slice();
	  newArray.splice(data.index,1);
	  deleteDataFromRebrandly(data.id, (json)=>{
		  this.setState({
			  getApi:newArray,
			  lastDelete:json,
			  choosenBtn:null
		  })
	  });
	  console.log("scelto il record:" + data.id );
  }
  
  componentDidMount(){
	  console.log("componente montato!");
	  getDataFromRebrandly((json)=>{
		  this.setState({
             getApi:json
		  })
	  });
  }

  handleChange(event) {
    let validUrl = false;
    if (event.target.value.length < 1) {
      validUrl = true;
    } else {
      validUrl = validateUrl(event.target.value);
    }
    this.setState({ input: event.target.value, validUrl: validUrl });
  }

  resetInput() {
    console.log("erase");
    this.setState({ input: "", validUrl: true });
  }

  render() {
    let inputLabel = classNames({
      "bg-white relative focus:outline-none  border-4  border-indigo-400 rounded md:rounded-lg py-2 px-4 block w-full appearance-none leading-normal": this
        .state.validUrl,
      "bg-red-100  relative focus:outline-none  border-4  border-red-400 rounded md:rounded-lg py-2 px-4 block w-full appearance-none leading-normal": !this
        .state.validUrl,
    });
    let labelMessage = classNames({
      "text-red-600 absolute ml-4 validUrlText uppercase visible": !this.state
        .validUrl,
      "invisible absolute": this.state.validUrl,
    });
    let xButton = classNames({
      "absolute hover:bg-indigo-500  mr-2 right-0 rounded-full flex justify-center items-center w-5 h-5 bg-indigo-300 text-white cursor-pointer": true,
      visible: this.state.input.length > 2,
      invisible: this.state.input.length < 2,
    });

    return (
      <div className="font-poppins">
        <Head />
        <div>
          {/* 	<!-- ###################### Hero Box  ################ --> */}
          <div className="py-8">
            <div className="container px-4 h-full flex-col flex md:flex-row mx-auto">
              <div className="block md:hidden hero-box bg-indigo-800"></div>
              <div className="py-8 justify-center text-center w-full md:text-left md:w-1/2 flex items-center">
                <div className="flex mt-10 md:mt-0 flex-col">
                  <p className="text-5xl font-bold leading-tight ">
                    More than just <br />
                    shorter links
                  </p>
                  <p className="mt-5 md:mt-2 text-gray-600 leading-relaxed  md:leading-snug">
                    Build your brand’s recognition and
                    <br className="block md:hidden" />
                    get detailed <br className="hidden md:block" />
                    insights on how your <br className="block md:hidden" />
                    links are performing.
                  </p>
                  <button className="mt-8 md:mt-4 button-rounded self-center md:self-start  px-8 py-2 ">
                    Get Started
                  </button>
                </div>
              </div>
              <div className="hidden md:block md:w-1/2 hero-box"></div>
            </div>
          </div>

          <div className="bg-gray-300 mt-20 pb-20">
            <div className="px-4">
              <div className="container relative h-full mx-auto">
                {/* 	<!-- #####################  Input Field  ###################### --> */}
                <Input>
                  <div className="boost float w-full absolute px-8 py-8 rounded-lg">
                    <div className="flex flex-col md:flex-row items-center">
                      <div className="flex-grow w-full md:w-auto ">
                        <div className="relative flex items-center">
                          <input
                            className={inputLabel}
                            type="text"
                            value={this.state.input}
                            onChange={(event) => this.handleChange(event)}
                            placeholder="Shorten a link here.."
                          />
                          <div
                            onClick={() => this.resetInput()}
                            /* 												 className="absolute hover:bg-indigo-500  mr-2 right-0 rounded-full flex justify-center items-center w-5 h-5 bg-indigo-300 text-white cursor-pointer
                             */ className={xButton}
                          >
                            {" "}
                            x
                          </div>
                          <div className={labelMessage}>
                            {" "}
                            Insert a valid Url
                          </div>
                        </div>
                      </div>
                      <div className="flex-grow-0 ml-0 md:ml-4 w-full md:w-auto mt-10 md:mt-0">
                        <button
                          onClick={() => this.postUrl()}
                          className="button rounded md:rounded-lg w-full px-4 py-2 "
                        >
                          Shorten It!
                        </button>
                      </div>
                    </div>
                  </div>
                </Input>

                <div className="pt-20 text-center">
                  {/* Inserire Component UrlList dove visualizzare la lista degli url accorciati */}
				  {this.state.getApi? <UrlList
					list={this.state.getApi}
					setCopyBtn={(i) => this.setCopyBtn(i)}
					deleteData= {(i)=> this.deleteApiData(i)}
                    choosenBtn={this.state.choosenBtn}
                  /> : "Loading.." }
                 

                  <p className="text-4xl font-bold mt-20">
                    Advanced Statistics
                  </p>
                  <p className="text-gray-600 mt-3">
                    Track how your links are performing across the web with
                    <br className="hidden md:block" />
                    our advanced statistics dashboard.
                  </p>

                  {/* <!-- #####################  Features Cards  ###################### --> */}

                  <div className="flex flex-col md:flex-row relative cube mt-10 ">
                    <div className="h-2 cyan w-full absolute self-center"></div>
                    <div className="bg-white relative mt-16 md:mt-0 rounded flex flex-col self-start text-center md:text-left  p-8 lg:py-12 lg:px-8">
                      <div className="rounded-full self-center md:self-start float-circle w-20 h-20  md:w-16 md:h-16 absolute bg-indigo-800 flex items-center justify-center">
                        <img
                          src="./images/icon-brand-recognition.svg"
                          alt="brand recognition"
                          className="h-8"
                        />
                      </div>
                      <p className="md:text-xl font-bold mt-6">
                        Brand Recognition
                      </p>
                      <p className="text-sm text-gray-600 mt-4 tracking-wider leading-relaxed">
                        Boost your brand recognition with each click. Generic
                        links don’t mean a thing. Branded links help instil
                        confidence in your content.
                      </p>
                    </div>

                    <div className="bg-white relative mt-16 md:mt-0 self-center rounded flex flex-col text-center md:text-left  p-8 lg:py-12 lg:px-8 md:mx-4">
                      <div className="rounded-full self-center md:self-start float-circle w-20 h-20  md:w-16 md:h-16 absolute bg-indigo-800 flex items-center justify-center">
                        <img
                          src="./images/icon-detailed-records.svg"
                          alt="brand recognition"
                          className="h-8"
                        />
                      </div>
                      <p className="md:text-xl font-bold mt-6">
                        Detailed Records
                      </p>
                      <p className="text-sm text-gray-600 mt-4 tracking-wider leading-relaxed">
                        Gain insights into who is clicking your links. Knowing
                        when and where people engage with your content helps
                        inform better decisions.
                      </p>
                    </div>

                    <div className="bg-white relative self-end mt-16 md:mt-0  rounded flex flex-col text-center md:text-left  p-8 lg:py-12 lg:px-8">
                      <div className="rounded-full self-center md:self-start float-circle w-20 h-20  md:w-16 md:h-16 absolute bg-indigo-800 flex items-center justify-center">
                        <img
                          src="./images/icon-fully-customizable.svg"
                          alt="brand recognition"
                          className="h-8"
                        />
                      </div>
                      <p className="md:text-xl font-bold mt-6">
                        Fully Customizable
                      </p>
                      <p className="text-sm text-gray-600 mt-4 tracking-wider leading-relaxed ">
                        Improve brand awareness and content discoverability
                        through customizable links, supercharging audience
                        engagement.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* 
			<!-- ###################### Boost your Link  ################ --> */}
        <div className="boost text-center text-white py-8">
          <p className="text-2xl">Boost your links today</p>
          <button className="button-rounded px-4 py-2 mt-5 ">
            Get Started
          </button>
        </div>

        {/* 		<!-- ###################### Footer ################ --> */}

        <Footer />
      </div>
    );
  }
}

export default App;
