import React from "react";
import classNames from "classnames";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import CancelIcon from "@material-ui/icons/Cancel";

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    padding: "10px",
    backgroundColor: "rgba(67, 65, 144, 0.80)",
    maxWidth: "none",
    color: "rgba(42, 207, 207, 0.87)",
    boxShadow: theme.shadows[1],
    fontSize: 14,
  },
}))(Tooltip);

const DelTooltip = withStyles((theme) => ({
  tooltip: {
    padding: "5px",
    backgroundColor: "rgba(67, 65, 144, 0.80)",
    maxWidth: "none",
    color: "rgba(42, 207, 207, 0.87)",
    boxShadow: theme.shadows[1],
    fontSize: 14,
  },
}))(Tooltip);

class SubList extends React.Component {
  render() {
    let choosenCopy = classNames({
      "w-full md:w-auto ml-0 md:ml-2": true,
      " btn text-white bg-indigo-700 pulse":
        this.props.index === this.props.choosenBtn,
      " btn btn-cyan btn-animate": !(
        this.props.index === this.props.choosenBtn
      ),
    });
    return (
      <li key={this.props.index} className="relative">
        <DelTooltip title="delete">
          <CancelIcon
            onClick={(i) =>
              this.props.deleteData({
                id: this.props.list[this.props.index].id,
                index: this.props.index,
              })
            }
            className="cursor-pointer absolute right-0  fill-current text-red-300 hover:text-red-600"
            style={{ fontSize: 20 }}
          />
        </DelTooltip>
        <div className="flex px-8 md:w-auto w-full md:flex-row flex-col  justify-between items-center bg-white rounded mb-4 p-2 shadow">
          <LightTooltip title={this.props.fullUrl}>
            <div className="cursor-pointer italic w-full md:w-auto text-left py-4 md:py-0 md:self-auto self-start border-gray-400 border-b md:border-0">
              {this.props.originalUrl}
            </div>
          </LightTooltip>

          <div className="flex md:flex-row flex-col items-center md:w-auto w-full">
            <div className="py-4 md:py-0 md:self-auto self-start">
              <a
                target="_blank"
                rel="noopener noreferrer"
                className="text-cyan opacity-75 hover:opacity-100"
                href={"https://" + this.props.list[this.props.index].shortUrl}
              >
                {this.props.list[this.props.index].shortUrl}
              </a>
            </div>
            <div className="relative self-stretch py-2 md:py-0">
              <a
                href="#"
                className={choosenCopy}
                onClick={(data) =>
                  this.props.copyToClipboard({
                    shortUrl: this.props.list[this.props.index].shortUrl,
                    index: this.props.index,
                  })
                }
              >
                {this.props.index === this.props.choosenBtn
                  ? "Copied"
                  : "Copy!"}
              </a>
            </div>
          </div>
        </div>
      </li>
    );
  }
}

class UrlList extends React.Component {
  copyToClipboard(data) {
    console.log("tasto scelto " + data.index);
    navigator.clipboard.writeText(data.shortUrl).then(
      function () {
        console.log("Async: Copying to clipboard was successful!");
      },
      function (err) {
        console.error("Async: Could not copy text: ", err);
      }
    );
    this.props.setCopyBtn(data.index);
  }

  // Function that fill an array with the url's list

  createUrlList(urlList) {
    var originalUrl;
    for (let i in this.props.list) {
      if (this.props.list[i].destination.length > 40) {
        originalUrl = this.props.list[i].destination.substring(0, 40) + "...";
      } else {
        originalUrl = this.props.list[i].destination;
      }

      urlList.push(
        <SubList
          index={i}
          key={i}
          fullUrl={this.props.list[i].destination}
          originalUrl={originalUrl}
          choosenBtn={this.props.choosenBtn}
          deleteData={(i) => this.props.deleteData(i)}
          list={this.props.list}
          copyToClipboard={(text) => this.copyToClipboard(text)}
        />
      );
    }
    return urlList;
  }

  render() {
    var urlList = [];
    urlList = this.createUrlList(urlList);
    return (
      <div className="mt-20 md:mt-0">
        <ul>{urlList}</ul>
      </div>
    );
  }
}

export default UrlList;
